/* CSS Reset END */

.disableSelection{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    outline: 0;
}


.admin-container {
    position: absolute;
    top: 50%;
    left: 85%;
    height: 80vh;
    width: 40vh;
    -webkit-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);

    -webkit-transition: all 0.5s ease !important;

    transition: all 0.5s ease !important;
    background-color: #343e48;
}

.buttons-container {
    position: absolute;
    height: 100%;
    width: 4vh;
    top: 50%;
    left: 4.8%;
    -webkit-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);
    -webkit-animation: 0.5s ease 0s 1 normal none running rotateportrait;
